@import url(https://fonts.googleapis.com/css?family=Poiret+One);

@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --rad: 0.7rem;
  --dur: 0.3s;
  --color-dark: #2f2f2f;
  --color-light: #fff;
  --color-brand: #57bd84;
  --font-fam: "Lato", sans-serif;
  --height: 5rem;
  --btn-width: 6rem;
  --bez: cubic-bezier(0, 0, 0.43, 1.49);
}

body {
  background-color: #f5f5f5;
  background: var(--color-dark);
  height: 100vh;
  min-height: 100vh;
  display: grid;
  place-items: center;
}

.widget {
  display: flex;
  height: 330px;
  width: 620px;
  flex-wrap: wrap;
  cursor: pointer;
  border-radius: 20px;
  box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.3),
    0 17px 17px 0 rgba(0, 0, 0, 0.15);

    @media (width <= 425px)
    {
        width: 290px;
        height: 340px;
        margin-left: 15px;
        
    }
    @media (width >= 425px)and (max-width:490px)
    {
     width:350px;
    }
    @media (width >= 491px)and (max-width:640px)
    {
     width:450px;
    }

  
}
.widget .weatherIcon {
  flex: 1 100%;
  height: 50%;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  background: #fafafa;
  font-family: weathericons;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 100px;
  @media (width <= 490px) 
  {
   font-size: 60px; 
   height: 34%;
  }
}

.widget .weatherInfo {
  flex: 0 0 70%;
  height: 40%;
  padding-left: 15px;
  background: #080705;
  display: flex;
  align-items: center;
  color: white;
  @media (width <= 490px) 
  {
    height: 35%;
    width: 290px;
   
  }

  @media (width >= 491px)and (max-width:640px)
  {
   width:290px;
  }

}
.widget .weatherInfo .temperature {
  flex: 0 0 40%;
  width: 100%;
  font-size: 65px;
  display: flex;
  justify-content: space-around;
  margin-right: 20px;
  @media (width <= 490px) 
  {
    width: 30%;
    font-size: 29px;  
  }
  @media (width >= 491px)and (max-width:640px)
  {
   width:30%;
   font-size: 45px;
  }

  
}
.widget .weatherInfo .description {
  flex: 0 60%;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: center;
  @media (width <= 490px) 
  {
    width: 40%;
     
  }
}
.widget .weatherInfo .description .weatherCondition {
  text-transform: uppercase;
  font-size: 35px;
  font-weight: 100;
  @media (width <= 490px) 
  {
 
    font-size: 19px;  
  }
  @media (width >= 491px)and (max-width:640px)
  {
   font-size: 38px;
  }

}
.widget .weatherInfo .description .place {
  font-size: 15px;
  @media (width <= 490px) 
  {
    font-size: 14x;  
  }
  @media (width >= 491px)and (max-width:640px)
  {
   font-size: 20px;
  }

}
.widget .date {
  flex: 0 0 30%;
  height: 40%;
  background: #048b9c;
 /* background: #70c1b3;*/
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white;
  font-size: 30px;
  font-weight: 800;
  text-align: center;
  @media (width <= 490px) 
  {
    height: 35%;
    font-size: 15px;  
  }
  @media (width >= 491px)and (max-width:640px)
  {
   font-size: 22px ;
  }

}

.widget .extra-temp {
  flex: 1 100%;
  padding: 5% 0;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background: #fafafa;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  font-size: 100px;
  @media (width <= 490px) 
  {
    height: 35%;  
  }
}

.widget .extra-temp p {
  font-size: 25px;
  font-weight: 600;
  @media (width <= 490px) 
  {
    
    font-size: 12px;  
  }
}

.temp-info-minmax,
.weather-extra-info,
.two-sided-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
  align-items: center;
  color: #70c1b3;
  /* background-color: blanchedalmond; */
}

.widget .extra-temp .extra-info-leftside {
  text-align: left;
  font-weight: 600;
  text-transform: capitalize;
  color: #080705;
  font-size: 15px;
  @media (width <= 490px) 
  {

    font-size: 10px;  
  }
}

/* input search button style  */

.search {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;

  @media (width>=426px) 
  {
    margin-top: 20px;
  }

}

.searchTerm {
  width: 50%;
  /* border: 3px solid #00b4cc; */
  border: none;
  padding: 16px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  font-size: 18px;
  @media (width <= 490px) 
  {
 
    font-size: 14px;  
  }
}

.searchTerm:focus {
  color: #00b4cc;
}

.searchButton {
  width: 80px;
  height: 32px;
  border: none;
  background: #048b9c;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
  outline: none;
  font-size: 16px;
  @media (width <= 490px) 
  {
   
    font-size: 14px;  
  }
}

.searchButton:hover {
  background-color: #17b8ce;
}

.wrap {
  display: flex;
  justify-content: center;
  text-align: center;
  margin-top: -2px;
  margin-bottom: 50px;
  
}

.copyright
{
    display: flex;
  justify-content: center;
  text-align: center;
margin-top: 24%;
  color: white;
  @media (width <= 490px) {
    font-size: 12px;
  }
}

